* {
	box-sizing: border-box;
}
html {
	font-size: 62.5%;
}
body {
	font-size: 1.6rem;
	background: #fff5da;
	font-family: 'Luckiest Guy', cursive;
}
img {
	width: 100%;
}
header {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	padding: 2.3rem 0 0.5rem;
	background: #fff5da;
	z-index: 1;
}
.logo {
	text-align: center;
	font-size: 6vw;
	text-transform: uppercase;
	opacity: 0;
	position: relative;
	left: 0;
	top: 0;
	width: 100%;
	color: #f47340;
	text-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
}
.menu {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 2rem;
	background: none;
	width: 3rem;
	height: 2rem;
	cursor: pointer;
	border: none;
	padding: 0;
	opacity: 0;
	@media (min-width: 730px) {
		right: 10%;
	}
	span {
		display: block;
		width: 100%;
		height: 2px;
		background: black;
		&:after,
		&:before {
			content: '';
			position: absolute;
			width: 100%;
			height: 2px;
			left: 0;
			background: inherit;
		}
		&:before {
			top: 0;
		}
		&:after {
			bottom: 0;
		}
	}
}
.o-wrapper {
	padding: 0 2rem;
	@media (min-width: 730px) {
		padding: 0 10%;
	}
}
.gallery {
	padding-top: 150vh;
	display: grid;
	list-style-type: none;
	grid-template-columns: repeat(2, minmax(0, 1fr));
	gap: 2rem;
	@media (min-width: 730px) {
		grid-template-columns: repeat(3, minmax(0, 1fr));
	}
}
.image {
	aspect-ratio: 2 / 3;
	img {
		height: 100%;
		object-fit: cover;
	}
}

footer {
	margin-top: 10rem;
	text-align: center;
	font-size: 6vw;
	letter-spacing: 0.1em;
	padding-bottom: 20rem;
	@media (min-width: 730px) {
		margin-top: 20rem;
		padding-bottom: 20rem;
	}
}
